<template>
<div class="recommend">

    <h3 class="recommend-title">为您推荐</h3>
    <van-skeleton avatar avatar-shape="square" avatar-size="92" :loading="loading">
		<div class="recommend-list" ref="recommendList">
            <ul class="list">
                <li class="recommend-item" v-for="r in recommendData" :key="r.id" @click="showDetailHandle(r.pid)">
                    <div class="recommend-img">
                        <img :src="r.smallImg" alt="推荐商品">
                    </div>
                    <h3 class="name">{{r.name}}</h3>
                    <div class="recommend-price">
                        <div class="price">{{r.price}}</div>
                        <div class="increase">+</div>
                    </div>
                </li>
            </ul>
		</div>
    </van-skeleton>
	<van-skeleton avatar avatar-shape="square" avatar-size="92" :loading="loading" />
	<van-skeleton avatar avatar-shape="square" avatar-size="92" :loading="loading" />
	<van-skeleton avatar avatar-shape="square" avatar-size="92" :loading="loading" />
</div>
</template>

<script>
import '../assets/scss/component/recommend.scss'
import BScroll from 'better-scroll'

export default {
    data() {
        return {
            recommendData: [],
            loading: true
        }
    },
    created() {
        this.getRecommendData();
    },
    beforeDestroy() {
        // 如果存在better-scroll 实例对象，则清除
        if (this.recomScroll) {
            this.recomScroll.destroy();
        }
    },
    methods: {
        // 查看商品详情
        showDetailHandle(pid) {
            this.$router.push({
                name: 'Detail',
                params: {
                    id: pid
                }
            })
        },
        // 获取推荐的商品
        getRecommendData() {

            this.axios({
                    method: 'GET',
                    url: '/typeProducts',
                    params: {
                        appkey: this.appkey,
                        key: 'isHot',
                        value: 1
                    }
                })
                .then(res => {
                    this.$toast.clear();
                    console.log(res);
                    if (res.data.code === 500) {
                        this.loading = false;
                        this.recommendData = res.data.result
                        this.initalBScroller();
                    }
                })
        },
        // 初始化BScroll
        initalBScroller() {
            this.$nextTick(() => {
                if (!this.recomScroll) {
                    // 创建better-scroll 实例化对象
                    this.recomScroll = new BScroll(this.$refs.recommendList, {
                        click: true, // 可点击子元素
                        scrollX: true
                    })

                } else {
                    // 页面刷新时防止重复实例化对象,调用刷新方法
                    this.recomScroll.refresh();
                }
            })
        }
    }
}
</script>
