<template>
<div class="home">
	<div class="time-title">
		{{timeText}}
		<span class="nick-name">{{userInfo.nickName}}</span>
		<div class="search-box" @click="search">
			<van-icon class="search-icon" size="14px" color="#ccc" name="search" />
			<span class="search-text">搜索</span>
		</div>
	</div>
	<van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
		<van-swipe-item v-for="(b,i) in banner" :key="i">
			<img class="banner-img" :src="b.url" alt="banner图">
		</van-swipe-item>
	</van-swipe>
	<ul>
		<li class="home-list-item border-bottom-1px">
			<div class="store-info">
				<h3 class="store-title">蚨来咖啡上海总店</h3>
				<p class="store-content">上海市-静安区-万荣路-大宁中心广场B6</p>
			</div>
		</li>
		<li class="home-list-item border-bottom-1px" @click="showMenuHandle">
			<div class="store-info">
				<h3 class="store-title">现在下单</h3>
				<p class="store-content">大约5分钟后取餐</p>
			</div>
			<div>
				<span class="buy-icon" />
			</div>
		</li>
	</ul>
	<Recommend />
</div>
</template>

<script>
import '../../assets/scss/home.scss'
import Recommend from '../../components/Recommend'
import {
	mapGetters
} from 'vuex'

export default {
	components: {
		Recommend
	},
	data() {
		return {
			timeText: '',
			banner: [
				{
					url: require('../../assets/images/banner01.png')
				},
				{
					url: require('../../assets/images/banner02.png')
				},
				{
					url: require('../../assets/images/banner03.png')
				}
			],
			checked: false,
			userInfo: {}
		}
	},
	created() {
		this.getTime();
		this.getUserInfo();

		if(this.getToken) {
			let params = {
				appkey: this.appkey,
				tokenString: this.getToken
			}

			this.$store.dispatch('getShopcartCount', params)
		}
	
	},
	methods: {
		// 搜索商品
		search() {
			this.$router.push({
				name: 'Search'
			})
		},
		getUserInfo() {
			if(!this.getToken) {
				return ;
			}

			let params = {
				appkey: this.appkey,
				tokenString: this.getToken
			}

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'GET',
				url: '/findMy',
				params
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				if(res.data.code === 'A001') {
					
					this.userInfo = res.data.result[0];
				}
				
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
				
			})
		},
		// 购买下单
		showMenuHandle() {
			this.$router.push({
				name: 'Menu'
			})
		},
		// 获取banner数据
		getBannerData() {

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'GET',
				url: '/banner',
				params: {
					appkey: this.appkey
				}
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				if(res.data.code === 300) {
					this.banner = res.data.result;
				}else {
					this.$notify({ 
						type: 'warning', 
						message: res.data.msg
					});
				}
			})
		},
		// 获取当前时间
		getTime() {
			let hours = new Date().getHours();
			
			if(hours >= 6 && hours < 12) {
				this.timeText = '上午好！'
			}
			if(hours >= 12 && hours < 18) {
				this.timeText = '下午好！'
			}
			if(hours >= 18 && hours < 24) {
				this.timeText = '晚上好！'
			}
			if(hours >= 0 && hours < 6) {
				this.timeText = '深夜好！'
			}
		}
	},
	computed: {
		...mapGetters(['getToken'])
	}
}
</script>
